import React, { useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { FiHome, FiCalendar, FiSettings, FiUsers, FiLogIn, FiUserPlus, FiLogOut } from 'react-icons/fi';

const Navigation = ({ isAuthenticated, onLogout }) => {
  const location = useLocation();
  const navigate = useNavigate();

  const handleLogout = () => {
    onLogout();
    navigate('/login');
  };

  return (
    <nav className="bg-gradient-to-r from-violet-600 via-purple-600 to-indigo-600 text-white shadow-lg">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          <div className="flex items-center">
            <Link to="/" className="flex items-center space-x-3">
              <span className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-white to-purple-200">
                EntntTracker
              </span>
            </Link>
          </div>

          <div className="hidden md:block">
            <div className="flex items-center space-x-4">
              {isAuthenticated ? (
                <>
                  {/* Navigation Links for authenticated users */}
                  {[
                    { path: '/', icon: FiHome, text: 'Dashboard' },
                    { path: '/calendar', icon: FiCalendar, text: 'Calendar' },
                    { path: '/companies', icon: FiUsers, text: 'Companies' },
                    { path: '/settings', icon: FiSettings, text: 'Settings' }
                  ].map(({ path, icon: Icon, text }) => (
                    <Link
                      key={path}
                      to={path}
                      className={`px-4 py-2 rounded-lg flex items-center space-x-2 transition-all duration-200 ${
                        location.pathname === path
                          ? 'bg-white/20 text-white'
                          : 'hover:bg-white/10 text-white/80'
                      }`}
                    >
                      <Icon className="w-5 h-5" />
                      <span>{text}</span>
                    </Link>
                  ))}
                  
                  {/* Logout Button */}
                  <button
                    onClick={handleLogout}
                    className="px-4 py-2 rounded-lg flex items-center space-x-2 bg-red-500 hover:bg-red-600 text-white transition-all duration-200"
                  >
                    <FiLogOut className="w-5 h-5" />
                    <span>Logout</span>
                  </button>
                </>
              ) : (
                <>
                  {/* Login/Signup buttons for non-authenticated users */}
                  <Link
                    to="/login"
                    className="px-4 py-2 rounded-lg flex items-center space-x-2 bg-white text-purple-600 hover:bg-purple-50 transition-all duration-200"
                  >
                    <FiLogIn className="w-5 h-5" />
                    <span>Login</span>
                  </Link>
                  <Link
                    to="/signup"
                    className="px-4 py-2 rounded-lg flex items-center space-x-2 bg-purple-500 text-white hover:bg-purple-600 transition-all duration-200"
                  >
                    <FiUserPlus className="w-5 h-5" />
                    <span>Sign Up</span>
                  </Link>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navigation; 