// App.js
import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider } from './context/ThemeContext';
import ErrorBoundary from './components/ErrorBoundary';
import LoadingSpinner from './components/LoadingSpinner';
import ThemeSelector from './components/ThemeSelector';
import { storageUtils } from './utils/storage';
import Login from './pages/auth/Login';
import SignUp from './pages/auth/SignUp';

// Import your components
import Dashboard from './pages/dashboard/Dashboard';
import CommunicationCalendar from './pages/calendar/CommunicationCalendar';
import CompanyManagement from './pages/admin/CompanyManagement';
import CommunicationSettings from './pages/admin/CommunicationSettings';
import Navigation from './components/Navigation';

function App() {
  const [isLoading, setIsLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userData, setUserData] = useState(null);

  useEffect(() => {
    const authStatus = storageUtils.getData('auth-status');
    if (authStatus) {
      setIsAuthenticated(true);
    }
    const savedData = storageUtils.getData('user-data');
    if (savedData) {
      setUserData(savedData);
    }
    setIsLoading(false);
  }, []);

  const handleLogin = () => {
    setIsAuthenticated(true);
    storageUtils.saveData('auth-status', true);
  };

  if (isLoading) {
    return <LoadingSpinner />;
  }

  return (
    <ErrorBoundary>
      <ThemeProvider>
        <Router>
          <div className="min-h-screen bg-gray-50">
            {isAuthenticated && <Navigation />}
            
            <main className="container mx-auto px-4 py-8">
              <Routes>
                <Route 
                  path="/login" 
                  element={
                    !isAuthenticated ? (
                      <Login onLogin={handleLogin} />
                    ) : (
                      <Navigate to="/" replace />
                    )
                  } 
                />
                <Route 
                  path="/signup" 
                  element={
                    !isAuthenticated ? (
                      <SignUp />
                    ) : (
                      <Navigate to="/" replace />
                    )
                  } 
                />
                
                {/* Protected Routes */}
                {isAuthenticated ? (
                  <>
                    <Route path="/" element={<Dashboard userData={userData} />} />
                    <Route path="/calendar" element={<CommunicationCalendar />} />
                    <Route path="/companies" element={<CompanyManagement />} />
                    <Route path="/settings" element={<CommunicationSettings />} />
                  </>
                ) : (
                  <Route path="*" element={<Navigate to="/login" replace />} />
                )}
              </Routes>
            </main>

            {isAuthenticated && <ThemeSelector />}
          </div>
        </Router>
      </ThemeProvider>
    </ErrorBoundary>
  );
}

export default App;
