import React, { useState } from 'react';
import { FiPlus, FiEdit2, FiTrash2, FiLinkedin, FiMail, FiPhone, FiCalendar, FiDownload } from 'react-icons/fi';
import CompanyModal from './CompanyModal';
import { CSVLink } from 'react-csv';
import { validateEmail, validatePhone, validateLinkedInUrl } from '../../utils/validators';

const CompanyManagement = () => {
  const [showModal, setShowModal] = useState(false);
  const [selectedCompany, setSelectedCompany] = useState(null);
  const [selectedCompanies, setSelectedCompanies] = useState([]);
  const [companies, setCompanies] = useState([
    {
      id: 1,
      name: 'Acme Corporation',
      location: 'New York, USA',
      linkedinUrl: 'https://linkedin.com/company/acme',
      emails: ['contact@acme.com', 'support@acme.com'],
      phones: ['+1 (555) 123-4567', '+1 (555) 765-4321'],
      communicationPeriod: 14,
      comments: 'Key strategic partner for Q2 2024',
      lastCommunication: '2024-02-15',
      nextCommunication: '2024-02-29',
      engagementScore: 85
    },
    // ... more companies
  ]);

  const handleBulkAction = (action) => {
    switch (action) {
      case 'communicate':
        // Open communication modal for selected companies
        break;
      case 'export':
        // Export selected companies data
        break;
      case 'delete':
        setCompanies(companies.filter(c => !selectedCompanies.includes(c.id)));
        setSelectedCompanies([]);
        break;
    }
  };

  const validateCompanyData = (data) => {
    const errors = [];
    
    // Validate emails
    data.emails.forEach(email => {
      if (!validateEmail(email)) {
        errors.push(`Invalid email: ${email}`);
      }
    });

    // Validate phone numbers
    data.phones.forEach(phone => {
      if (!validatePhone(phone)) {
        errors.push(`Invalid phone number: ${phone}`);
      }
    });

    // Validate LinkedIn URL
    if (!validateLinkedInUrl(data.linkedinUrl)) {
      errors.push('Invalid LinkedIn URL');
    }

    return errors;
  };

  const handleSave = (data) => {
    const validationErrors = validateCompanyData(data);
    if (validationErrors.length > 0) {
      alert(`Please fix the following errors:\n${validationErrors.join('\n')}`);
      return;
    }

    if (data.id) {
      setCompanies(companies.map(c => c.id === data.id ? data : c));
    } else {
      setCompanies([...companies, { ...data, id: Date.now() }]);
    }
    setShowModal(false);
  };

  const handleDelete = (companyId) => {
    if (window.confirm('Are you sure you want to delete this company?')) {
      setCompanies(companies.filter(c => c.id !== companyId));
      setSelectedCompanies(selectedCompanies.filter(id => id !== companyId));
    }
  };

  // Prepare CSV data for export
  const csvData = companies.map(company => ({
    Name: company.name,
    Location: company.location,
    LinkedIn: company.linkedinUrl,
    Emails: company.emails.join(', '),
    Phones: company.phones.join(', '),
    'Communication Period': `${company.communicationPeriod} days`,
    'Last Communication': company.lastCommunication,
    'Next Communication': company.nextCommunication,
    'Engagement Score': company.engagementScore,
    Comments: company.comments
  }));

  const getStatusColor = (status) => {
    switch (status) {
      case 'active':
        return 'bg-green-100 text-green-800';
      case 'inactive':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="space-y-6">
      {/* Header with bulk actions */}
      <div className="sm:flex sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-semibold text-gray-900">Company Management</h1>
          <p className="mt-2 text-sm text-gray-700">
            Manage your company communications and relationships
          </p>
        </div>
        <div className="mt-4 sm:mt-0 flex space-x-3">
          {selectedCompanies.length > 0 && (
            <>
              <button
                onClick={() => handleBulkAction('communicate')}
                className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
              >
                <FiMail className="mr-2 -ml-1 h-5 w-5" />
                Bulk Communicate
              </button>
              <CSVLink
                data={csvData}
                filename="companies-export.csv"
                className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
              >
                <FiDownload className="mr-2 -ml-1 h-5 w-5" />
                Export Selected
              </CSVLink>
              <button
                onClick={() => handleBulkAction('delete')}
                className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700"
              >
                <FiTrash2 className="mr-2 -ml-1 h-5 w-5" />
                Delete Selected
              </button>
            </>
          )}
          <button
            onClick={() => {
              setSelectedCompany(null);
              setShowModal(true);
            }}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700"
          >
            <FiPlus className="mr-2 -ml-1 h-5 w-5" />
            Add Company
          </button>
        </div>
      </div>

      {/* Enhanced Company Grid */}
      <div className="bg-white shadow overflow-hidden sm:rounded-md">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                <input
                  type="checkbox"
                  checked={selectedCompanies.length === companies.length}
                  onChange={(e) => {
                    setSelectedCompanies(
                      e.target.checked ? companies.map(c => c.id) : []
                    );
                  }}
                  className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                />
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Company Info
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Contact Details
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Communication Status
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {companies.map((company) => (
              <tr key={company.id} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">
                  <input
                    type="checkbox"
                    checked={selectedCompanies.includes(company.id)}
                    onChange={(e) => {
                      setSelectedCompanies(
                        e.target.checked
                          ? [...selectedCompanies, company.id]
                          : selectedCompanies.filter(id => id !== company.id)
                      );
                    }}
                    className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                  />
                </td>
                <td className="px-6 py-4">
                  <div className="flex items-center">
                    <div>
                      <div className="text-sm font-medium text-gray-900">
                        {company.name}
                      </div>
                      <div className="text-sm text-gray-500">
                        {company.location}
                      </div>
                      <a
                        href={company.linkedinUrl}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="inline-flex items-center mt-1 text-sm text-blue-600 hover:text-blue-800"
                      >
                        <FiLinkedin className="mr-1 h-4 w-4" />
                        LinkedIn Profile
                      </a>
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4">
                  <div className="text-sm text-gray-900">
                    {company.emails.map((email, idx) => (
                      <div key={idx} className="flex items-center">
                        <FiMail className="mr-1 h-4 w-4 text-gray-400" />
                        {email}
                      </div>
                    ))}
                    {company.phones.map((phone, idx) => (
                      <div key={idx} className="flex items-center">
                        <FiPhone className="mr-1 h-4 w-4 text-gray-400" />
                        {phone}
                      </div>
                    ))}
                  </div>
                </td>
                <td className="px-6 py-4">
                  <div className="text-sm text-gray-900">
                    <div className="flex items-center">
                      <FiCalendar className="mr-1 h-4 w-4 text-gray-400" />
                      Every {company.communicationPeriod} days
                    </div>
                    <div className="mt-1">
                      <div className="text-xs text-gray-500">
                        Last: {new Date(company.lastCommunication).toLocaleDateString()}
                      </div>
                      <div className="text-xs text-gray-500">
                        Next: {new Date(company.nextCommunication).toLocaleDateString()}
                      </div>
                    </div>
                    <div className="mt-2">
                      <div className="flex items-center">
                        <span className="text-xs font-medium text-gray-500">
                          Engagement Score:
                        </span>
                        <span className={`ml-1 text-xs font-semibold ${
                          company.engagementScore >= 80 ? 'text-green-600' :
                          company.engagementScore >= 60 ? 'text-yellow-600' :
                          'text-red-600'
                        }`}>
                          {company.engagementScore}%
                        </span>
                      </div>
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <button
                    onClick={() => {
                      setSelectedCompany(company);
                      setShowModal(true);
                    }}
                    className="text-blue-600 hover:text-blue-900 mr-4"
                  >
                    <FiEdit2 className="h-5 w-5" />
                  </button>
                  <button
                    onClick={() => handleDelete(company.id)}
                    className="text-red-600 hover:text-red-900"
                  >
                    <FiTrash2 className="h-5 w-5" />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Enhanced Company Modal */}
      {showModal && (
        <CompanyModal
          company={selectedCompany}
          onClose={() => setShowModal(false)}
          onSave={handleSave}
        />
      )}
    </div>
  );
};

export default CompanyManagement; 